import React, { Component } from 'react';
import styled, {css } from 'styled-components';
import QustionTable from './QustionTable';
import Button from '@material-ui/core/Button'
import {connect} from 'react-redux';
import {bindActionCreators} from 'redux';
import * as customerActions from '../../modules/customer';
import QuestionForm from './QuestionForm';
import Dimmed from '../Dimmed';

const Wrapper = styled.div`
    padding-top : 80px;
    min-width : 1200px;
    width : 100%;
    height : 100%;
    background-color : white;
`

const Content = styled.div`
    width : 75%;
    height : 100%;
    margin : 0 auto;
    background-color : white;
    padding-top : 1rem;

    .title {
        width : 100%;
        margin : 0 auto;
        margin-bottom : 1rem;
        padding : 1rem;

        border-top : 2px solid #5b5555;
        border-bottom : 2px solid #5b5555;
        font-size : 20px;
        font-weight : 600;

        .sub-title {
            display : inline-block;
            margin-left : 2rem;
            font-size: 13px;
        }
    }

    .write-btn {
        float: right;
        overflow: hidden;
        margin-bottom: 5px;
        height: 30px;
        font-size: 15px;
        line-height: 20px;
        width: 110px;
    }
`
class CounselingQuestion extends Component {
    handleWrite = () => {
        const {CustomerActions} = this.props;
        CustomerActions.openModal();
    }
    render() {
        const data = {
            seq : 1,
            title : '불면증에 대한 상담은 어떤식으로 이루어지나요?',
            writer : 'godchoii',
            date : '2019.05.02'
        }
        const {
            CustomerActions,
            CustomerState,
            AuthState
        } = this.props;

        const {modal} = CustomerState; 

        return (
            <Wrapper>
                <Content>
                    <div className="title">상담 문의 <span className="sub-title">상담사와 소통하는 공간입니다</span></div>
                    <Button className="write-btn" variant="contained" color="primary" onClick={this.handleWrite}>
                        글 작성
                    </Button>
                    <QustionTable
                        data={data}/>
                </Content>
                <Dimmed visible={modal.visible}/>
                <QuestionForm 
                    visible={modal.visible} 
                    CustomerActions={CustomerActions}
                    id={AuthState.loginInfo.userId}
                    name={AuthState.loginInfo.userName}/>
            </Wrapper>
        );
    }
}

export default connect(
    (state) => ({
        CustomerState : state.customer.toJS(),
        AuthState : state.authentication.toJS()
    }),
    (dispatch) => ({
        CustomerActions : bindActionCreators(customerActions,dispatch)
    })
)(CounselingQuestion);