const Wrapper = styled.div`
    position : fixed;
    top : 50%; left : 50%;
    transform : translate(-50%, -50%);
`

const ButtonBox = styled.div`
    width : 500px;
    height : 500px;
    display : flex;
    flex-direction : column;
    justify-content : center;

    .btn {
        width : 100%;
    }
    
`

class AdminPage extends Component {
    render() {
        return (
            <Wrapper>
                <ButtonBox>
                    <Link to={"/main/customer/question"}><Button variant="contained" className="btn">고객 문의 처리</Button></Link>
                    <Link to={"/admin/main"}><Button variant="contained" className="btn">관리 페이지</Button> </Link>
                </ButtonBox>
            </Wrapper>
        );
    }
}

export default AdminPage;