import React, { Component } from 'react';
import styled from 'styled-components';

const Wrapper = styled.div`
    font-size : 25px;
    font-weight : 600;
    padding : 100px;
`
class NotFound extends Component {
    render() {
        return (
            <Wrapper>
                404 NOT FOUND
            </Wrapper>
        );
    }
}

export default NotFound;